import * as React from 'react';

import DeveloperToolsConsoleActionCreator from './../ActionCreators/DeveloperToolsConsoleActionCreator';
import DeveloperToolsConsoleStore from './../Stores/DeveloperToolsConsoleStore';
import DeveloperToolsTab from './../Models/DeveloperToolsTab';
import LoadingOrErrorModule from './DeveloperModules/LoadingOrErrorModule/LoadingOrErrorModule';
import {
  Pivot,
  PivotLinkSize,
  PivotLinkFormat,
  PivotItem,
  Button,
  ButtonType
} from './../DeveloperToolsFabric';

import styles from './DeveloperToolsConsole.module.scss';

import strings from './../DeveloperTools.resx';

export interface IDeveloperToolsConsoleState {
  visible: boolean;
  topPosition: number;
  tabs: DeveloperToolsTab[];
  selectedTabId: number;
}

export default class DeveloperToolsConsole extends React.Component<void, IDeveloperToolsConsoleState> {
  constructor() {
    super();
    this.state = {
      topPosition: DeveloperToolsConsoleStore.height,
      visible: DeveloperToolsConsoleStore.visible,
      tabs: DeveloperToolsConsoleStore.tabs,
      selectedTabId: DeveloperToolsConsoleStore.selectedTabId
    };
  }

  public componentDidMount(): void {
    DeveloperToolsConsoleStore.addListener(this._setState);
  }

  public componentWillUnmount(): void {
    DeveloperToolsConsoleStore.removeListener(this._setState);
  }

  public render(): React.ReactElement<{}> {
    if (this.state.visible) {
      const tabs: JSX.Element[] = this.state.tabs.map((tab: DeveloperToolsTab, id: number) => {
        return  <PivotItem linkText={tab.title}>
                  <LoadingOrErrorModule tab={tab} />
                </PivotItem>;
      });

      return  <div  className={styles.container}
                    style={{ height: `${this.state.topPosition * 100}%` }}>
                <div  className={styles.resizeBar}
                      draggable={true}
                      onDrag={this._onDrag}>
                </div>
                <div className={styles.toolsArea}>
                  <Pivot  linkSize={PivotLinkSize.normal}
                          linkFormat={PivotLinkFormat.links}>
                    {tabs}
                  </Pivot>

                  <Button
                      className={styles.closeButton}
                      buttonType={ButtonType.icon}
                      icon='Cancel'
                      rootProps={{ title: strings.closeDeveloperToolsAriaLabel }}
                      ariaLabel={strings.closeDeveloperToolsAriaLabel}
                      onClick={(): void => DeveloperToolsConsoleActionCreator.showHideConsole(false)} />
                </div>
              </div>;
    } else {
      return null; // tslint:disable-line:no-null-keyword
    }
  }

  private _onDrag: (e: React.DragEvent) => void = (e: React.DragEvent): void => {
    const mouseEvent: React.MouseEvent = (e as any) as React.MouseEvent; // tslint:disable-line:no-any

    if (mouseEvent.pageY !== 0) {
      const height: number = (document.documentElement.clientHeight - mouseEvent.pageY) /
                             document.documentElement.clientHeight;
      DeveloperToolsConsoleActionCreator.resizeConsole(height);
    }
  };

  private _setState: () => void = (): void => {
    this.setState({
      topPosition: DeveloperToolsConsoleStore.height,
      visible: DeveloperToolsConsoleStore.visible,
      tabs: DeveloperToolsConsoleStore.tabs,
      selectedTabId: DeveloperToolsConsoleStore.selectedTabId
    });
  };
}
